body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Home-image {
  /* The image used */
  background-image: url(/static/media/BackgroundPicture.c6dc587a.jpg);

  /* Full height */
  height: 100vh;
  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: repeat;
  background-size: cover;
  background-color: rgba(0,0,0,0.5); 
  z-index: 10;
}
.Home-overlay {
  height: 100%;
  width: 100%;
  background-color: rgba(46, 46, 48, 0.5);
}
.AppointmentPage-image {
    /* The image used */
    background-image: url(/static/media/AppointmentImage.0a6660f7.png);
  
    /* Full height */
    min-height: 100vh;
    /* Center and scale the image nicely */
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: fixed;
    background-repeat: repeat;
    background-size: cover;
    background-color: rgba(0,0,0,0.5); 
    z-index: 10;
  }
  .AppointmentPage-overlay {
    min-height: 100vh;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  }
  .AppointmentPage-SmallBoxOverLay {
    height: 100%;
    margin-right: 5%;
    margin-left: 5%;
    background-color: rgba(255, 255, 255, 0.507);
  }
  .AppointmentPage-newInPanel{
      -webkit-align-content: space-around;
              align-content: space-around;
      margin-left: 20%;
  }
  .AppointmentHistory-h1 {
      color: white;
      -webkit-animation: flyin 1.5s ease forwards;
              animation: flyin 1.5s ease forwards;
      opacity: 0;
      -webkit-transform: scale(2);
              transform: scale(2);
      -webkit-filter: blur(4px);
              filter: blur(4px);
    }
    
    @-webkit-keyframes flyin {
       to { 
         -webkit-filter: blur(0); 
                 filter: blur(0);
         -webkit-transform: scale(1);
                 transform: scale(1);
         opacity: 1;
       }
  }
    
    @keyframes flyin {
       to { 
         -webkit-filter: blur(0); 
                 filter: blur(0);
         -webkit-transform: scale(1);
                 transform: scale(1);
         opacity: 1;
       }
  }
